@import "../../scss/mixins/rem";

.page {
  $block-name: &; // #{$block-name}__element

  display: flex;
  flex-direction: column;
  min-height: 100svh;
  background: var(--back-primary);

  @media screen and (min-width: $screen-d) {
    overflow: hidden;
    border-radius: rem(16px);
  }

  &__header {
    flex: 0 0 auto;
  }

  &__main {
    flex: 1 0 auto;
  }

  &__footer {
    flex: 0 0 auto;

    @media screen and (min-width: $screen-tl) {
      display: none;
    }
  }
}
