@import "sanitize.css/sanitize.css";
@import "sanitize.css/forms.css";
@import "sanitize.css/assets.css";
@import "sanitize.css/typography.css";
@import "sanitize.css/reduce-motion.css";
:root {
  --color-black: #000;
  --color-white: #fff;
  --body-color: var(--front-primary);
  --body-bg: var(--front-secondary);
  --font-base: var(--font-sans);
  --font-sans:
    "PT Root UI", -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Ubuntu",
    "Droid Sans", "Helvetica Neue", Arial, sans-serif, "Apple Color Emoji", "Segoe UI Emoji",
    "Segoe UI Symbol";
  --font-serif: Georgia, "Times New Roman", Times, serif;
  --font-mono: SFMono-Regular, Menlo, Monaco, Consolas, Liberation Mono, "Courier New", monospace;
  --font-size-root: 100%;
  --font-size-base: 1rem;
  --font-weight-base: 400;
  --line-height-base: 1.25;
  --front-primary: #464e5f;
  --front-secondary: #abb0c3;
  --front-tertiary: #c9cfe5;
  --front-invert: #fff;
  --back-primary: #fff;
  --back-secondary: #f0f5fa;
  --back-tertiary: #e1ecf5;
  --color-red: #ee2b49;
  --color-orange: #f2712d;
  --color-yellow: #fbb726;
  --color-green: #0da46f;
  --color-light-blue: #06a1d7;
  --color-blue: #3156a6;
  --color-dark-blue: #102e6a;
  --color-violet: #6b59a5;
}

:root {
  font-size: var(--font-size-root);
}

html,
body {
  height: 100%;
}

html {
  min-width: 320px;
  min-height: 100%;
  font-size: var(--font-size-root);
  font-family: sans-serif;
  line-height: normal;
  -webkit-tap-highlight-color: rgb(var(--color-black) 0);
  -webkit-text-size-adjust: 100%;
}

body {
  margin: 0;
  color: var(--body-color);
  font-weight: var(--font-weight-base);
  font-size: var(--font-size-base);
  font-family: var(--font-base);
  line-height: var(--line-height-base);
  background-color: var(--body-bg);
}

svg {
  display: block;
}

[tabindex="-1"]:focus:not(:focus-visible) {
  outline: 0;
}

[hidden] {
  display: none;
}

@font-face {
  font-weight: 400;
  font-family: "PT Root UI";
  font-style: normal;
  font-display: swap;
  src: url("/fonts/pt-root-ui_regular.woff2") format("woff2");
}
@font-face {
  font-weight: 500;
  font-family: "PT Root UI";
  font-style: normal;
  font-display: swap;
  src: url("/fonts/pt-root-ui_medium.woff2") format("woff2");
}
@font-face {
  font-weight: 700;
  font-family: "PT Root UI";
  font-style: normal;
  font-display: swap;
  src: url("/fonts/pt-root-ui_bold.woff2") format("woff2");
}
.footer {
  padding: 1.4375rem 0 1.5rem;
  border-top: 0.0625rem solid var(--back-tertiary);
}
.footer__inner {
  display: grid;
  gap: 1rem;
  justify-items: center;
}
.footer__copy {
  font-weight: 500;
  font-size: 0.875rem;
  line-height: 1.1429;
}
.footer__links {
  display: grid;
  gap: 0.5rem;
  justify-content: center;
}
.footer__link {
  font-weight: 500;
  font-size: 0.75rem;
  line-height: 1;
  color: var(--front-secondary);
  text-align: center;
  text-decoration: none;
}

.grid {
  width: calc(100% - 2rem);
  margin: 0 auto;
}
@media screen and (min-width: 576px) {
  .grid {
    width: calc(100% - 4rem);
  }
}
@media screen and (min-width: 1280px) {
  .grid {
    width: calc(100% - 5rem);
  }
}

.header {
  background: var(--back-primary);
  border-bottom: 0.0625rem solid var(--back-tertiary);
}
.header__inner {
  display: flex;
  align-items: center;
  justify-content: space-between;
  padding: 0.75rem 0 0.6875rem;
}
@media screen and (min-width: 768px) {
  .header__inner {
    padding: 1.25rem 0 1.1875rem;
  }
}
@media screen and (min-width: 992px) {
  .header__inner {
    gap: 2rem;
  }
}
@media screen and (min-width: 1280px) {
  .header__inner {
    gap: 2.5rem;
  }
}
@media screen and (min-width: 992px) {
  .header__logo {
    width: 7.5rem;
  }
}
@media screen and (min-width: 1280px) {
  .header__logo {
    width: 12rem;
  }
}
.header__nav {
  display: none;
  gap: 1.5rem;
}
@media screen and (min-width: 992px) {
  .header__nav {
    display: flex;
    margin-right: auto;
  }
}
@media screen and (min-width: 1280px) {
  .header__nav {
    gap: 2.5rem;
  }
}
.header__nav-item {
  font-weight: 500;
  font-size: 1rem;
  line-height: 1.25;
  color: inherit;
  text-decoration: none;
}
.header__actions {
  display: flex;
  gap: 1rem;
  align-items: center;
}
.header__action {
  display: flex;
  align-items: center;
  justify-content: center;
  position: relative;
  padding: 0.5625rem;
  border: 0.0625rem solid var(--front-tertiary);
  border-radius: 1.25rem;
}
@media screen and (min-width: 992px) {
  .header__action {
    padding: 0.8125rem;
    border-radius: 1.5rem;
  }
}
.header__action--balls {
  font-weight: 400;
  font-size: 1rem;
  line-height: 1.25;
  display: none;
  gap: 0.25rem;
}
@media screen and (min-width: 992px) {
  .header__action--balls {
    display: flex;
    padding: 0.8125rem 1.6875rem;
  }
}
.header__action--notification[data-noty=true]::before {
  position: absolute;
  top: -0.1875rem;
  right: -0.1875rem;
  display: block;
  width: 0.5rem;
  height: 0.5rem;
  background: var(--color-yellow);
  border-radius: 50%;
  content: "";
}
.header__action--user {
  overflow: hidden;
}
.header__action--logout {
  display: none;
}
@media screen and (min-width: 992px) {
  .header__action--logout {
    display: block;
  }
}
.header__balls {
  font-weight: 700;
  font-size: 1rem;
  line-height: 1.25;
}
.header__avatar {
  position: absolute;
  z-index: 2;
  display: block;
  width: 100%;
  height: 100%;
}
.header__icon {
  width: 1.25rem;
  height: 1.25rem;
  fill: none;
}

.logo {
  display: none;
  width: 8.75rem;
  aspect-ratio: 111/80;
}
@media screen and (min-width: 992px) {
  .logo {
    display: block;
    width: 6.9375rem;
  }
}
.logo--mobile {
  display: block;
  aspect-ratio: 140/40;
}
@media screen and (min-width: 992px) {
  .logo--mobile {
    display: none;
  }
}

.page {
  display: flex;
  flex-direction: column;
  min-height: 100svh;
  background: var(--back-primary);
}
@media screen and (min-width: 1280px) {
  .page {
    overflow: hidden;
    border-radius: 1rem;
  }
}
.page__header {
  flex: 0 0 auto;
}
.page__main {
  flex: 1 0 auto;
}
.page__footer {
  flex: 0 0 auto;
}
@media screen and (min-width: 992px) {
  .page__footer {
    display: none;
  }
}

